@import url("https://fonts.googleapis.com/css2?family=Crimson+Pro:wght@200&family=Lora:wght@500;700&family=Montserrat:wght@400;500;700&display=swap");
:root {
  --bg_color: hsla(270, 4%, 9%, 1);
  --white: hsl(0, 0%, 100%);
  --footer: hsla(0, 0%, 66%, 1);
  --card_subtitle: hsla(0, 0%, 51%, 1);
  --box_shadow: hsla(0, 0%, 0%, 0.251);
}

.logo {
  font-family: "Crimson Pro";
  font-style: normal;
  font-weight: 200;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--white);
}

.menu {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: var(--white);
}
.menu__item:hover {
  font-weight: 700;
}

.main__title {
  font-family: "Lora";
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 46px;
}
.main__paragraph {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}
.main__link {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
}

.card__title {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
}
.card__subtitle {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  color: var(--card_subtitle);
}
.card__main {
  font-family: "Lora";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 23px;
}

.footer {
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.0625rem;
}
.footer__paragraph {
  color: var(--footer);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: var(--white);
}

body {
  background-color: var(--bg_color);
  max-height: 100vh;
}

a {
  text-decoration: none;
}

.container {
  padding: 0.75rem;
}
@media (min-width: 768px) {
  .container {
    padding: 2rem 1.25rem;
  }
}

.card__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.card {
  display: flex;
  flex-direction: column;
  padding: 0.823125rem 1.4175rem 1.5625rem;
  max-height: 8.625rem;
  max-width: 18.75rem;
  box-shadow: 0px 0.25rem 0.25rem 0px var(--box_shadow);
  background-color: var(--bg_color);
  align-self: center;
  position: absolute;
  bottom: -3.5rem;
  z-index: 1000;
  gap: 1.36875rem;
}
@media (min-width: 768px) {
  .card {
    bottom: -5rem;
    right: 1.8rem;
  }
}
.card__header {
  display: flex;
  gap: 20px;
  width: 100%;
  height: 2.25rem;
  align-items: center;
}
.card__title-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.card__photo {
  width: 2.25rem;
  height: 2.25rem;
  border-radius: 50%;
}
.card__main {
  width: 13.125rem;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  margin-top: 6.875rem;
  z-index: 1000;
}
.footer__paragraph {
  position: absolute;
  padding-bottom: -0.75rem;
}
.footer__anchor {
  box-shadow: inset 0 0 0 0 hsl(0deg, 0%, 35%);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.footer__anchor:hover {
  box-shadow: inset 100px 0 0 0 hsl(0deg, 0%, 35%);
  color: hsl(0deg, 0%, 80%);
}

.menu {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px;
  visibility: hidden;
  background-color: var(--bg_color);
  transform: scale(0, 0);
  transform-origin: top right;
  transition: all ease-in-out 0.3s;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100vw;
  height: 100vh;
  z-index: 1001;
}
@media (min-width: 768px) {
  .menu {
    display: flex;
    position: static;
    width: 60%;
    height: inherit;
    transform: scale(1, 1);
    transform-origin: right;
    justify-content: flex-end;
    align-items: flex-end;
    visibility: visible;
  }
}
.menu--active {
  position: fixed;
  transform: scale(1, 1);
  visibility: visible;
}
.menu__list {
  height: 90%;
  padding: 100px 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .menu__list {
    flex-direction: row;
    padding: 0;
    width: 80%;
    justify-content: space-between;
  }
}
.menu__item {
  list-style: none;
}
.menu__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  border-bottom: solid 2px var(--white);
  position: relative;
  top: 8px;
  transform: scaleX(0);
  transition: transform ease-in-out 0.3s;
}
.menu__item:hover::after {
  transform: scaleX(1);
}
.menu__icon {
  display: block;
  align-self: flex-end;
  width: 22px;
  height: 22px;
  transform: scale(1, 1);
  transition: transform ease-in-out 0.3s;
  cursor: pointer;
}
@media (min-width: 768px) {
  .menu__icon {
    display: none !important;
  }
}
.menu__icon:hover {
  transform: scale(1.5, 1.5);
}

.header {
  max-height: 3.125rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
@media (min-width: 768px) {
  .header__icon {
    display: none !important;
  }
}

.logo {
  border: solid 1px var(--white);
  padding: 0.5rem;
  width: 8.125rem;
  height: 2rem;
}

.main {
  width: 100%;
  max-height: 36.625rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.5625rem;
}
@media (min-width: 768px) {
  .main {
    flex-direction: row;
    align-items: center;
    margin-top: 6.25rem;
  }
}
.main__link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0.9375rem;
}
.main__image {
  width: 100%;
}